import {
	request
} from '@/utils/api/request.js';

let baseAPI = "/user"

// 
export const userLogin = (options) => {
	// return axios.post('/api/user/login', qs.stringify(data))
	return request({
		url: baseAPI + '/login',
		method: 'POST',
		data: options
	})
}

// 
export const getUserInfoList = (options) => {
	return request({
		url: baseAPI + '/queryUserInfoList',
		method: 'GET',
		data: options
	})
}


