import {
	request
} from '@/utils/api/request.js';

let baseAPI = "/paper"


// 
export const getChatList = (options) => {
	return request({
		url: baseAPI + '/getChatList',
		method: 'GET',
		data: options
	})
}

//
export const submitChat = (options) => {
	return request({
		url: baseAPI + '/submitChat',
		method: 'POST',
		data: options
	})
}


//
export const getPaperList = (options) => {
	return request({
		url: baseAPI + '/getPaperList',
		method: 'GET',
		data: options
	})
}
