// 

export default {
	// 
	getNetwork(){
		uni.getNetworkType({
			success(res) {
				if (res.networkType === 'none') {
					uni.$u.toast(',')
				}
			}
		})
	},
	// 
	onNetwork() {
		uni.onNetworkStatusChange(res => {
			// 
			if (!res.isConnected) {
				uni.$u.toast(',')
				return uni.$u.route('/pages/mine/login')
			}
			// 
			switch (res.networkType) {
				case 'unknown':
					uni.$u.toast('')
					break;
				case 'wifi':
					uni.$u.toast('wifi')
					break;
				case '4g':
					uni.$u.toast(',')
					break;
				case '3g':
					uni.$u.toast(',')
					break;
				case '2g':
					uni.$u.toast(',')
					break;
				case 'none':
					uni.$u.toast(',')
					break;
			}
		})
	},
	// 
	update(){































	}
}
